<script>
import moment from 'moment'
import Datepicker from 'vuejs-datepicker'
import 'intro.js/introjs.css'
import introJs from 'intro.js'
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import idLocale from '@fullcalendar/core/locales/id'
import 'moment/locale/id';

export default {
    data() {
        return{
            pelanggan : [],
            historyPesanan: [],
            title: ['Bapak', 'Ibu', 'Kakak'],
            templateWhatsApp: [],
            templateContentWhatsApp: ['title', 'name'],
            modaladdPelanggan: false,
            modaleditPelanggan: false,
            modalHistoryPesanan: false,
            modaldetailHistoryPesanan: false,
            modallistTemplateWhatasApp: false,
            modaladdTemplateWhatsApp: false,
            modaleditTemplateWhatsApp: false,
            modalMessage: false,
            modalPreviewMessage: false,
            searchTemplatePesan: '',
            filteredTemplateWhatsApp: [],
            form: {
                kode: '',
                title: '',
                nama: '',
                rumah: '',
                kantor: '',
                wa: '',
                catatan: ''
            },
            formHistoryPesanan: {
                bulan: ''
            },
            formSendWhatsApp : {
                nama_panggilan: '',
                nama: '',
                hp: '',
                title: '',
                content: ''
            },
            formdetailHistory: {
                nama: '',
                tipe: '',
                wa: '',
                tanggal_pesan: '',
                tanggal_ambil: '',
                total: ''
            },
            calendarOptions: {
                plugins: [ dayGridPlugin, interactionPlugin ],
                initialView: 'dayGridMonth',
                locale: idLocale,
                height: 700,
                // showNonCurrentDates: false,
                eventClick: this.handleEventClick,
                initialDate: new Date(),
                events: []
            },
        }
    },

    components:{
        Datepicker,
        introJs,
        FullCalendar
    },

    computed: {
        validateForm() {
        return !this.errors.any()
            && this.form.title != ''
            && this.form.nama != ''
            && this.form.wa != ''
            && this.form.catatan != ''
        },

        validateHistoryPesanan() {
        return !this.errors.any()
            && this.formHistoryPesanan.bulan != ''
        },
    },

    mounted() {
        this.loadData();
    },

    methods:{
        loadData(){
            this.$vs.loading();
            this.$http.get('pos/pelanggan/getData')
            .then(response => {
                this.pelanggan = response.data.message
            })
            .catch(err=>{
                this.$vs.loading.close()
                this.$swal.fire(
                    'Gagal !',
                    err.response.data.message,
                    'error'
                )
            })
            .finally(() => {this.$vs.loading.close()});
        },

        loadTemplateWhatsApp() {
            this.$vs.loading();
            this.$http.get('pos/chat/template')
            .then(response => {
                this.templateWhatsApp = response.data.message;
                this.filteredTemplateWhatsApp = this.templateWhatsApp;
            })
            .catch(err=>{
                this.$vs.loading.close()
                this.$swal.fire(
                    'Gagal !',
                    err.response.data.message,
                    'error'
                )
            })
            .finally(() => {this.$vs.loading.close()});
        },

        loadDataHistoryPesanan() {
            const now = new Date(this.formHistoryPesanan.bulan);
            const year = now.getFullYear();
            const month = String(now.getMonth() + 1).padStart(2, '0');
            const day = String(now.getDate()).padStart(2, '0');

            this.$vs.loading();
            this.$http.get('pos/pesanan/getData/' + `${year}-${month}`)
            .then(response => {
                this.historyPesanan = response.data.message;
                this.$refs.calendar.getApi().gotoDate(`${year}-${month}-${day}`);
                this.calendarOptions.events = this.transformDataForCalendar(this.historyPesanan);
            })
            .catch(err=>{
                this.$vs.loading.close();
                this.$swal.fire(
                    'Gagal !',
                    err.response.data.message,
                    'error'
                )
            })
            .finally(() => {this.$vs.loading.close()});
        },

        transformDataForCalendar(data) {
            return data.map(event => ({
                nama: event.nama,
                wa: event.wa,
                tipe: event.tipe,
                tanggal_pesan: event.tanggal_pesan,
                tanggal_ambil: event.tanggal_ambil,
                total: event.total,
                title: `${event.nama} - ${event.tipe}`,
                start: event.tanggal_pesan,
            }))
        },

        handleEventClick(info) {
            this.modaldetailHistoryPesanan = true;
            this.formdetailHistory.nama =  info.event.extendedProps.nama;
            this.formdetailHistory.tipe =  info.event.extendedProps.tipe;
            this.formdetailHistory.wa =  info.event.extendedProps.wa;
            this.formdetailHistory.tanggal_pesan =  info.event.extendedProps.tanggal_pesan;
            this.formdetailHistory.tanggal_ambil =  info.event.extendedProps.tanggal_ambil;
            this.formdetailHistory.total =  info.event.extendedProps.total;
        },

        addPelanggan(){
            this.modaladdPelanggan = true,
            this.form.title = ''
            this.form.nama = ''
            this.form.wa = ''
            this.form.rumah = ''
            this.form.kantor = ''
            this.form.catatan = ''
        },

        editPelanggan(kode){
            this.modaleditPelanggan = true;

            let hasil = this.pelanggan.filter(item => {
                return item.kode.includes(kode);
            });

            this.form.kode = hasil[0].kode;
            this.form.title = hasil[0].title;
            this.form.nama = hasil[0].nama;
            this.form.rumah = hasil[0].rumah;
            this.form.kantor = hasil[0].kantor;
            this.form.wa = hasil[0].wa;
            this.form.catatan = hasil[0].catatan;
        },

        addMesaage(nama_panggilan, nama, number) {
            this.modalMessage = true;
            this.formSendWhatsApp.nama_panggilan = nama_panggilan;
            this.formSendWhatsApp.nama = nama;
            this.formSendWhatsApp.hp = number;
            this.searchTemplatePesan = '';
            this.loadTemplateWhatsApp();
        },

        previewMessage(item) {
            this.modalPreviewMessage = true;
            let narasi = item.narasi.replace("[title]", this.formSendWhatsApp.nama_panggilan).replace("[name]", this.formSendWhatsApp.nama);
            this.formSendWhatsApp.content = narasi;
        },

        sendMessage() {
            let url = `https://wa.me/${this.formSendWhatsApp.hp}?text=${encodeURI(this.formSendWhatsApp.content)}`;
            let newTab = window.open(url, '_blank');
            newTab.focus();
            this.modalPreviewMessage = false;
        },

        listTemplateWhatsApp() {
            this.modallistTemplateWhatasApp = true;
            this.loadTemplateWhatsApp();
        },

        addTemplateWhatsApp() {
            this.modaladdTemplateWhatsApp = true;
            this.formSendWhatsApp.content = '';
        },

        editTemplateWhatsApp(title) {
            this.modaleditTemplateWhatsApp = true;
            this.formSendWhatsApp.title = '';
            this.formSendWhatsApp.content = '';

            let hasil = this.templateWhatsApp.filter(item => {
                return item.title.includes(title);
            });

            this.formSendWhatsApp.title = hasil[0].title;
            this.formSendWhatsApp.content = hasil[0].narasi;

        },

        removeTemplateWhatsApp(title) {
            this.$swal.fire({
                title: 'Apakah Anda Yakin ?',
                text: "Ingin Menghapus Data Template WhatsApp",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Ya'
            })
            .then((result) => {
                if (result.value) {
                    this.$vs.loading();
                    this.$http.delete('pos/chat/template/' + encodeURI(title))
                    .then(response=> {
                        if(response.data.status == "SUKSES"){
                            this.$swal.fire({
                                title: 'Success!',
                                text: response.data.message,
                                icon: 'success',
                                timer: 3000,
                                showConfirmButton: false
                            }).then(() => {
                                this.loadTemplateWhatsApp();
                            });
                        }else {
                            this.$swal.fire(
                                'Gagal !',
                                response.data.message,
                                'error'
                            )
                        }
                    })
                    .catch(err=>{
                        this.$vs.loading.close();
                        this.$swal.fire(
                            'Gagal !',
                            err.response.data.message,
                            'error'
                        )
                    })
                    .finally(() => {this.$vs.loading.close()});
                }
            });
        },

        updateTemplateWhatsApp() {
            this.$vs.loading();
            const postData = {
                title: this.formSendWhatsApp.title,
                narasi: this.formSendWhatsApp.content,
            }

            this.$http.post('pos/chat/template/update', postData)
            .then(response=> {
                if(response.data.status == "SUKSES"){
