        editContentMessage(item) {
            this.formSendWhatsApp.content = this.formSendWhatsApp.content + ' ' +  '[' + item + ']'
            this.formSendWhatsApp.content = this.formSendWhatsApp.content.trim();
        },

        filterTemplateWhatsApp() {
            const searchTextLower = this.searchTemplatePesan.toLowerCase();
            this.filteredTemplateWhatsApp = this.templateWhatsApp.filter(item => {
                return item.title.toLowerCase().includes(searchTextLower) ||
                    item.narasi.toLowerCase().includes(searchTextLower);
            });
        },

        addData(){
            this.$vs.loading();
            const postData = {
                title: this.form.title,
                nama: this.form.nama,
                rumah: this.form.rumah,
                wa: this.form.wa,
